<template>
  <div class="container my-5">
    <div class="my-5">
      <h3><i class="fas fa-file-export"></i> Carga de Registros</h3>
    </div>
    <div class="row">
      <div class="col-12 col-sm-6 col-md-4 mb-2">
        <select class="form-control">
          <option selected>Seleccione una opción</option>
          <option value="1">Clientes</option>
          <option value="2">Productos</option>
        </select>
      </div>
      <div class="col-12 col-sm-6 col-md-4">
        <button @click="generarArchivo" class="btn btn-primary col-12">
          Exportar archivo base
        </button>
      </div>
    </div>
    <hr class="mt-2 mb-3" />
    <div class="row">
      <div class="col-12 col-sm-6 col-md-4 mb-2">
        <i class="fas fa-file-upload"></i
        ><span class="name">No file selected</span>
        <input type="file" class="my_input" id="upload" />
      </div>
      <div class="col-12 col-sm-6 col-md-4">
        <button class="btn btn-primary col-12" @click="importarArchivo">
          Importar Registros
        </button>
      </div>
    </div>
    <hr class="mt-2 mb-3" />
  </div>
</template>

<script>
// import xlsx from "xlsx";

export default {
  data() {
    return {
      arrayList: [],
    };
  },
  methods: {
    generarArchivo() {
      console.log("bien");
      const Toast = this.$swal.mixin({
        toast: true,
        position: "top-end",
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
          console.log("ENTRO");
          toast.addEventListener("mouseenter", this.$swal.stopTimer);
          toast.addEventListener("mouseleave", this.$swal.resumeTimer);
        },
      });

      Toast.fire({
        icon: "success",
        title: "Descargando archivo .xls base",
      });
    },
    importarArchivo() {
      const Toast = this.$swal.mixin({
        toast: true,
        position: "top-end",
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
          console.log("ENTRO");
          toast.addEventListener("mouseenter", this.$swal.stopTimer);
          toast.addEventListener("mouseleave", this.$swal.resumeTimer);
        },
      });

      Toast.fire({
        icon: "success",
        title: "Importando registros",
      });
    },
  },
};
</script>

<style>
.element {
  display: inline-flex;
  align-items: center;
}
i.fa-file-upload {
  margin: 10px;
  cursor: pointer;
  font-size: 30px;
}
i:hover {
  opacity: 0.6;
}
input {
  display: none;
}

.button-wrapper {
  position: relative;
  width: 150px;
  text-align: center;
  margin: 20% auto;
}

.button-wrapper span.label {
  position: relative;
  z-index: 0;
  display: inline-block;
  width: 100%;
  background: #00bfff;
  cursor: pointer;
  color: #fff;
  padding: 10px 0;
  text-transform: uppercase;
  font-size: 12px;
}

#upload {
  display: inline-block;
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 50px;
  top: 0;
  left: 0;
  opacity: 0;
  cursor: pointer;
}
</style>
